import { ReportTypeCode } from '../enums/ReportTypeCode';
import { TimeUnitCode } from '../enums/TimeUnitCode';
import { ReportDataTypeCode } from '../enums/ReportDataTypeCode';
import { ReportFormatCode } from '../enums/ReportFormatCode';

/*  
*/
export class ReportGenerationRequestBody {
 
    // Filter by sensor(s) 
    public sensorIds: string[];
 
    // Filter by folder(s) 
    public folderIds: string[];
 
    // Filter report after the provided timestamp [Epoch milliseconds Timestamp] 
    public from: number;
 
    // Filter report before the provided timestamp [Epoch milliseconds Timestamp] 
    public to: number;
 
    // The desired type of the report. Must be defined 
    public reportType: ReportTypeCode;
 
    // The time period over which to aggregate the data. Supports UNDEFINED (raw data), HOUR, DAY and WEEK. 
    public aggregationType: TimeUnitCode;
 
    // Selection for the type of report to generate, defines the amount of data (number of columns) contained in the report. 
    public dataType: ReportDataTypeCode;
 
    // Selection for the format of the report. 
    public reportFormat: ReportFormatCode;
 
    // Hour of first data to include in report, 0-23 are valid options 
    public surveillanceStartHour: number;
 
    constructor(sensorIds?: string[], folderIds?: string[], from?: number, to?: number, reportType?: ReportTypeCode, aggregationType?: TimeUnitCode, dataType?: ReportDataTypeCode, reportFormat?: ReportFormatCode, surveillanceStartHour?: number) { 
        this.sensorIds = sensorIds;
        this.folderIds = folderIds;
        this.from = from;
        this.to = to;
        this.reportType = reportType;
        this.aggregationType = aggregationType;
        this.dataType = dataType;
        this.reportFormat = reportFormat;
        this.surveillanceStartHour = surveillanceStartHour;
    }

}


