import { SocketOpCode } from '../enums/SocketOpCode';
import { SocketEventsFilterPayload } from '../sockets/SocketEventsFilterPayload';

/* 
   @WebSocketMessage
   This message is sent to the server to set filter criteria for the events 
*/
export class SocketEventsFilter {
 
    // Message OpCode (1030) 
    public op: SocketOpCode;
 
    // Message payload 
    public payload: SocketEventsFilterPayload;
 
    constructor(op?: SocketOpCode, payload?: SocketEventsFilterPayload) { 
        this.op = op;
        this.payload = payload;
    }

}


