
/*  
*/
export class EngineInformation {
 
    // 
    public engineVersion: string;
 
    // 
    public deploymentId: string;
 
    // 
    public numberOfPOIsUsed: number;
 
    // 
    public numberOfPOIsLicensed: number;
 
    constructor(engineVersion?: string, deploymentId?: string, numberOfPOIsUsed?: number, numberOfPOIsLicensed?: number) { 
        this.engineVersion = engineVersion;
        this.deploymentId = deploymentId;
        this.numberOfPOIsUsed = numberOfPOIsUsed;
        this.numberOfPOIsLicensed = numberOfPOIsLicensed;
    }

}


