import { LevelTypeCode } from '../enums/LevelTypeCode';

/*  
*/
export class SearchServiceSimilarRequest {
 
    // Object Id to look for similar objects 
    public objectId: number;
 
    // Sensor Id where the object was detected 
    public sensorId: string;
 
    // Timestamp when the object was detected (or relative time in milliseconds for negative number) 
    public timestamp: number;
 
    // Search time scope start time (Epoch milliseconds timestamp or relative time in milliseconds for negative number) 
    public from: number;
 
    // Search time scope end time (Epoch milliseconds timestamp or relative time in milliseconds for negative number) 
    public to: number;
 
    // Level of similarity tolerance (LOW | MEDIUM | HIGH) 
    public similarity: LevelTypeCode;
 
    constructor(objectId?: number, sensorId?: string, timestamp?: number, from?: number, to?: number, similarity?: LevelTypeCode) { 
        this.objectId = objectId;
        this.sensorId = sensorId;
        this.timestamp = timestamp;
        this.from = from;
        this.to = to;
        this.similarity = similarity;
    }

}


