import { SocketOpCode } from '../enums/SocketOpCode';
import { SocketEventNotificationPayload } from '../sockets/SocketEventNotificationPayload';

/* 
   @WebSocketMessage
   This message is pushed to the client upon any new event in the registered account 
*/
export class SocketEventNotification {
 
    // Message OpCode (1031) 
    public op: SocketOpCode;
 
    // Message payload 
    public payload: SocketEventNotificationPayload;
 
    constructor(op?: SocketOpCode, payload?: SocketEventNotificationPayload) { 
        this.op = op;
        this.payload = payload;
    }

}


