
/*  
*/
export class TrafficAnalysisReportRequest {
 
    // Filter by sensor(s) 
    public sensorId: string[];
 
    // Filter by folder(s) 
    public folderId: string[];
 
    // Filter report after the provided timestamp (RFC3339 datetime string, URL-encoded) Example "2022-01-02T13%3A14%3A15%2B00%3A00". Notice that the colon is URL-encoded and values must include leading zeros 
    public from: string;
 
    // Filter report before the provided timestamp (RFC3339 datetime string, URL-encoded) Example "2022-01-02T13%3A14%3A15%2B00%3A00". Notice that the colon is URL-encoded and values must include leading zeros 
    public to: string;
 
    constructor(sensorId?: string[], folderId?: string[], from?: string, to?: string) { 
        this.sensorId = sensorId;
        this.folderId = folderId;
        this.from = from;
        this.to = to;
    }

}


