
/* 
   An item in the sensors usage report's sensors list 
*/
export class SensorUsageReport {
 
    // Sensor 'key' 
    public key: string;
 
    // the total minutes the sensor was active during the report period 
    public minutes: number;
 
    // Is the sensor billable ( If was active 15 or more minutes during at least one day of the report period ) 
    public billable: boolean;
 
    constructor(key?: string, minutes?: number, billable?: boolean) { 
        this.key = key;
        this.minutes = minutes;
        this.billable = billable;
    }

}


