import { UserAccountInfoAccount } from '../common/UserAccountInfoAccount';
import { LoginData } from '../common/LoginData';

/* 
   User Account info (returned by switch-account method) 
*/
export class UserAccountInfo {
 
    // Account data 
    public account: UserAccountInfoAccount;
 
    // Login data 
    public loginData: LoginData;
 
    // List of features enabled for this account 
    public features: string[];
 
    // When license will be expired [Epoch milliseconds Timestamp] 
    public licenseExpiresOn: number;
 
    constructor(account?: UserAccountInfoAccount, loginData?: LoginData, features?: string[], licenseExpiresOn?: number) { 
        this.account = account;
        this.loginData = loginData;
        this.features = features;
        this.licenseExpiresOn = licenseExpiresOn;
    }

}


