
/*  
*/
export class AppliancesServiceExportSensorsRequest {
 
    // Appliance Id 
    public id: string;
 
    // Export results to a different format. Supported formats: csv | json | xml 
    public format: string;
 
    // Set download file name (without extension) 
    public fileName: string;
 
    constructor(id?: string, format?: string, fileName?: string) { 
        this.id = id;
        this.format = format;
        this.fileName = fileName;
    }

}


