import { ModelAttributes } from '../common/ModelAttributes';
import { ObjectTypeCode } from '../enums/ObjectTypeCode';
import { SearchColor } from '../common/SearchColor';
import { ObjectData } from '../common/ObjectData';

/* 
   Object attributes to search for 
*/
export class SearchObject {
 
    // Object type to search for: PERSON | CAR | MOTORCYCLE ... 
    public type: ObjectTypeCode;
 
    // Optional object colors to search for 
    public colors: SearchColor[];
 
    // Used for similarity behavior 
    public objectData: ObjectData;
 
    // External models attributes 
    public externalModelResults: ModelAttributes[];
 
    constructor(type?: ObjectTypeCode, colors?: SearchColor[], objectData?: ObjectData, externalModelResults?: ModelAttributes[]) { 
        this.type = type;
        this.colors = colors;
        this.objectData = objectData;
        this.externalModelResults = externalModelResults;
    }

}


