import { SearchEvent } from '../entities/SearchEvent';
import { BaseEntity } from '../entities/BaseEntity';

/* 
   Case entity represents investigation case to collect related search events 
*/
export class CaseInfo extends BaseEntity {
 
    // Account Id 
    public accountId: string;
 
    // Case name 
    public name: string;
 
    // Case description 
    public description: string;
 
    // User Id who created the case 
    public userId: string;
 
    // Case Tag (for organizing search definitions in folder-like structure) 
    public tag: string;
 
    // Is the case access is private (accessible by owner only) or public (accessible by all account users) 
    public isPrivate: boolean;
 
    // List of related search events data in this case 
    public events: SearchEvent[];
 
    // Entity Id 
    public id: string;
 
    // Entity document type (system field) 
    public _type: string;
 
    // When the entity was created [Epoch milliseconds Timestamp] 
    public createdOn: number;
 
    // When the entity was updated [Epoch milliseconds Timestamp] 
    public updatedOn: number;
 
    // Entity path in the hierarchy 
    public path: string;
 

}


