
/*  
*/
export class AddPoiRequestBody {
 
    // The image to use, must be a base64 encoded jpeg containing a single person 
    public base64EncodedJpeg: string;
 
    // A name to give the POI (optional) 
    public name: string;
 
    // The required expiration time of the POI, in milliseconds. Must be larger than zero. 
    public ttl: number;
 
    // An optional ID of an already created POI to update. If empty then a new POI will be created. 
    public poiId: string;
 
    constructor(base64EncodedJpeg?: string, name?: string, ttl?: number, poiId?: string) { 
        this.base64EncodedJpeg = base64EncodedJpeg;
        this.name = name;
        this.ttl = ttl;
        this.poiId = poiId;
    }

}


