/* 
   Attributes mask - the bit mask represents the combination of object's attributes 
*/
export enum AttributesMask {
 
    // Undefined [0] 
    UNDEFINED = 0,
 
    // Unknown Face [65536] 
    ATTR_FACE_UNKNOWN = 65536,
 
    // Face with mask [65792] 
    ATTR_FACE_MASK = 65792,
 
    // Unknown gender [131072] 
    ATTR_GENDER_UNKNOWN = 131072,
 
    // Male [131328] 
    ATTR_GENDER_MALE = 131328,
 
    // Female [131584] 
    ATTR_GENDER_FEMALE = 131584,
 
    // Unknown age [196608] 
    ATTR_AGE_UNKNOWN = 262144,
 
    // Age of 0-4 [196864] 
    ATTR_AGE_0_4 = 262400,
 
    // Age of 4-8 [197120] 
    ATTR_AGE_4_8 = 262656,
 
    // Age of 8-15 [197632] 
    ATTR_AGE_8_15 = 263168,
 
    // Age of 15-25 [198656] 
    ATTR_AGE_15_25 = 264192,
 
    // Age of 25-35 [200704] 
    ATTR_AGE_25_35 = 266240,
 
    // Age of 35-45 [204800] 
    ATTR_AGE_35_45 = 270336,
 
    // Age of 45-60 [212992] 
    ATTR_AGE_45_60 = 278528,
 
    // Age of 60 or more [229376] 
    ATTR_AGE_60_PLUS = 294912,
 
}