import { PoiBoundingBox } from '../messages/PoiBoundingBox';

/*  
*/
export class IdentifiedPoiFromImage {
 
    // The given ID of the POI 
    public poiId: string;
 
    // The given name to the POI 
    public displayName: string;
 
    // The score of the identified POI. Higher value is more likely to be correct 
    public score: number;
 
    // 
    public boundingBox: PoiBoundingBox;
 
    constructor(poiId?: string, displayName?: string, score?: number, boundingBox?: PoiBoundingBox) { 
        this.poiId = poiId;
        this.displayName = displayName;
        this.score = score;
        this.boundingBox = boundingBox;
    }

}


