import { BaseEntity } from '../entities/BaseEntity';

/* 
   Unrecognized Point of Interest (POI) type 
*/
export class PoiUnrecognized extends BaseEntity {
 
    // POI ID - mapped to poi_id field in ES 
    public poiId: string;
 
    // Display name for the POI (e.g., "Unrecognized") 
    public displayName: string;
 
    // Base64 encoded display image 
    public displayImg: string;
 
    // List of watchlists this POI belongs to 
    public poiWatchlists: string[];
 
    // Feature embedding vector (512 dimensions) 
    public embedding: number[];
 
    // Recognition/confidence score 
    public score: number;
 
    // Age estimate 
    public age: number;
 
    // Gender (1=male, 0=female) 
    public gender: number;
 
    // Event timestamp in UTC [Epoch milliseconds Timestamp] 
    public timestamp: number;
 
    // Creation timestamp 
    public createdAt: number;
 
    // Last update timestamp 
    public updatedAt: number;
 
    // Entity Id 
    public id: string;
 
    // Entity document type (system field) 
    public _type: string;
 
    // When the entity was created [Epoch milliseconds Timestamp] 
    public createdOn: number;
 
    // When the entity was updated [Epoch milliseconds Timestamp] 
    public updatedOn: number;
 
    // Entity path in the hierarchy 
    public path: string;
 

}


