import { AccountRoleCode } from '../enums/AccountRoleCode';

/* 
   Account member registration data model - used by self registered users 
*/
export class MemberRegistration {
 
    // Account Id 
    public accountId: string;
 
    // User name 
    public name: string;
 
    // User email 
    public email: string;
 
    // User mobile phone number (for notification and validation) 
    public mobile: string;
 
    // Member role in the account 
    public role: AccountRoleCode;
 
    // List of permission groups 
    public groups: string[];
 
    // Flag to send email verification to the user 
    public verifyByEmail: boolean;
 
    // User description 
    public description: string;
 
    constructor(accountId?: string, name?: string, email?: string, mobile?: string, role?: AccountRoleCode, groups?: string[], verifyByEmail?: boolean, description?: string) { 
        this.accountId = accountId;
        this.name = name;
        this.email = email;
        this.mobile = mobile;
        this.role = role;
        this.groups = groups;
        this.verifyByEmail = verifyByEmail;
        this.description = description;
    }

}


