import { ObjectTypeCode } from '../enums/ObjectTypeCode';
import { BehaviorTypeCode } from '../enums/BehaviorTypeCode';
import { SeverityTypeCode } from '../enums/SeverityTypeCode';
import { EntityTypeCode } from '../enums/EntityTypeCode';

/* 
   List of filter criteria for conditional integration action 
*/
export class IntegrationActionFilter {
 
    // Activate integration only if the sensor id is in the list (empty list means no filter) 
    public sensorIds: string[];
 
    // Activate integration only if the rule id is in the list (empty list means no filter) 
    public ruleIds: string[];
 
    // Activate integration only if the object type code is in the list (empty list means no filter) 
    public objectTypes: ObjectTypeCode[];
 
    // Activate integration only if the object type code is in the list (empty list means no filter) 
    public behaviorTypes: BehaviorTypeCode[];
 
    // Activate integration only if the event severity matching this value 
    public severity: SeverityTypeCode;
 
    // Activate integration only for specific subject type: SENSOR | AGENT | APPLIANCE (For health event only) 
    public subjectType: EntityTypeCode;
 
    // Activate integration only for specific subject status (For health event only) 
    public subjectStatus: number;
 
    // Activate integration only for specific subject status (For health event only) 
    public subjectState: number;
 
    constructor(sensorIds?: string[], ruleIds?: string[], objectTypes?: ObjectTypeCode[], behaviorTypes?: BehaviorTypeCode[], severity?: SeverityTypeCode, subjectType?: EntityTypeCode, subjectStatus?: number, subjectState?: number) { 
        this.sensorIds = sensorIds;
        this.ruleIds = ruleIds;
        this.objectTypes = objectTypes;
        this.behaviorTypes = behaviorTypes;
        this.severity = severity;
        this.subjectType = subjectType;
        this.subjectStatus = subjectStatus;
        this.subjectState = subjectState;
    }

}


