import { BaseEntity } from '../entities/BaseEntity';
import { EntityTypeCode } from '../enums/EntityTypeCode';
import { IntegrationTypeCode } from '../enums/IntegrationTypeCode';

/* 
   Describes status of an integration attempt (for events and health integrations) - was the integration successfully triggered or not.
   This contains information on what type of event the integration was triggered by (even or health event),
   the name and id of the integration target, the t 
*/
export class IntegrationStatusResponse extends BaseEntity {
 
    // What triggered this event/health event (can be only EVENT or HEALTH_EVENT) 
    public source: EntityTypeCode;
 
    // Account Id 
    public accountId: string;
 
    // Id of the triggering event or health event 
    public entityId: string;
 
    // Integration target Id 
    public integrationTargetId: string;
 
    // Integration target Name 
    public integrationTargetName: string;
 
    // Integration type (HTTP | HTTPS | SMTP | IMMIX | SENTINEL | GENETEC ...) 
    public integrationTargetType: IntegrationTypeCode;
 
    // Integration action Id 
    public integrationActionId: string;
 
    // Integration action Name 
    public integrationActionName: string;
 
    // Number of attempts made before the event was sent, or failed to send 
    public numberOfAttempts: number;
 
    // Sent is true if the messages was successfully sent to the integration target 
    public sent: boolean;
 
    // Possible error message in case of failure 
    public message: string;
 
    // Entity Id 
    public id: string;
 
    // Entity document type (system field) 
    public _type: string;
 
    // When the entity was created [Epoch milliseconds Timestamp] 
    public createdOn: number;
 
    // When the entity was updated [Epoch milliseconds Timestamp] 
    public updatedOn: number;
 
    // Entity path in the hierarchy 
    public path: string;
 

}


