
/*  
*/
export class GetPoiFromImageRequestBody {
 
    // The image to use to search for a face and identify a POI. Must be a Base64 encoded jpeg image containing a single face. 
    public base64EncodedJpeg: string;
 
    // set for search unrecognized poi 
    public searchUnrecognized: boolean;
 
    constructor(base64EncodedJpeg?: string, searchUnrecognized?: boolean) { 
        this.base64EncodedJpeg = base64EncodedJpeg;
        this.searchUnrecognized = searchUnrecognized;
    }

}


