import { PermissionMask } from '../enums/PermissionMask';

/* 
   Set of actions allowed on folder path 
*/
export class Permission {
 
    // Folder path 
    public path: string;
 
    // Folder permission 
    public perm: PermissionMask;
 
    constructor(path?: string, perm?: PermissionMask) { 
        this.path = path;
        this.perm = perm;
    }

}


