import { Sensitivity } from '../common/Sensitivity';
import { ObjectTypeCode } from '../enums/ObjectTypeCode';

/* 
   Sensor anomaly rule information 
*/
export class SensorAnomalyRuleInfo {
 
    // Indicates if the rule is active (default) or inactive 
    public active: boolean;
 
    // Sensitivity (for Anomaly) 
    public sensitivity: Sensitivity;
 
    // Object types: PERSON | VEHICLE | MOTORCYCLE ... 
    public objectTypes: ObjectTypeCode[];
 
    // Flag to indicate if event image should be created 
    public createImage: boolean;
 
    // Flag to indicate if event clip should be created 
    public createClip: boolean;
 
    // Id of the Anomaly Rule 
    public ruleId: string;
 
    // Rule external id (how the rule is identified in other system) 
    public externalId: string;
 
    constructor(active?: boolean, sensitivity?: Sensitivity, objectTypes?: ObjectTypeCode[], createImage?: boolean, createClip?: boolean, ruleId?: string, externalId?: string) { 
        this.active = active;
        this.sensitivity = sensitivity;
        this.objectTypes = objectTypes;
        this.createImage = createImage;
        this.createClip = createClip;
        this.ruleId = ruleId;
        this.externalId = externalId;
    }

}


