/* 
   Detected object behavior type code
   Values with INNOVI prefix represents standard innoVi model behaviour, all other values represent custom models behaviour 
*/
export enum BehaviorTypeCode {
 
    // Undefined 0 
    UNDEFINED = 0,
 
    // Crossing a Line 65793 
    INNOVI_CROSSING = 65793,
 
    // Moving in an area 65794 
    INNOVI_MOVING = 65794,
 
    // Stopped vehicle 65796 
    INNOVI_STOPPED = 65796,
 
    // Crowd density 65800 
    INNOVI_CROWD_DENSITY = 65800,
 
    // Grouping 65808 
    INNOVI_GROUPING = 65808,
 
    // Unattended Object 65824 
    INNOVI_UNATTENDED_OBJECT = 65824,
 
    // Asset protection 65856 
    INNOVI_ASSET_PROTECTION = 65856,
 
    // Traffic statistics report 65857 
    INNOVI_TRAFFIC_STATISTICS = 65857,
 
    // Count statistics report 65858 
    INNOVI_COUNT_STATISTICS = 65858,
 
    // Counter flow 65859 
    INNOVI_COUNTER_FLOW = 65859,
 
    // Ignore Mask 66049 
    INNOVI_MASK_IGNORE = 66049,
 
    // Shutdown Mask 66050 
    INNOVI_MASK_SHUTDOWN = 66050,
 
    // General Anomaly 66560 
    INNOVI_ANOMALY = 66560,
 
    // Similar Object 67584 
    INNOVI_SIMILARITY = 67584,
 
    // Smoke and Fire 67585 
    INNOVI_SMOKEANDFIRE = 67585,
 
    // Face Present 67586 
    INNOVI_FACE = 67586,
 
    // Slip and Fall 65860 
    INNOVI_SLIP_AND_FALL = 65860,
 
    // Crowd Statistics 65861 
    INNOVI_CROWD_STATISTICS = 65861,
 
}