import { ObjectTypeCode } from '../enums/ObjectTypeCode';

/*  
*/
export class SensorHeatmapRequest {
 
    // Search session Id 
    public sessionId: string;
 
    // Sensor Id 
    public sensorId: string;
 
    // Filter search events after the provided timestamp (epoch milliseconds or relative time in milliseconds for negative number) 
    public from: number;
 
    // Filter search events before the provided timestamp (epoch milliseconds or relative time in milliseconds for negative number) 
    public to: number;
 
    // Filter by object type(s) 
    public objectType: ObjectTypeCode[];
 
    // Number of cells in theX and Y dimensions 
    public dim: number;
 
    constructor(sessionId?: string, sensorId?: string, from?: number, to?: number, objectType?: ObjectTypeCode[], dim?: number) { 
        this.sessionId = sessionId;
        this.sensorId = sensorId;
        this.from = from;
        this.to = to;
        this.objectType = objectType;
        this.dim = dim;
    }

}


