import { ObjectInstance } from '../common/ObjectInstance';

/* 
   Object Metadata info 
*/
export class ObjectInfo {
 
    // Object tracking Id (This is not unique id across the system) 
    public objectId: number;
 
    // Sensor id 
    public sensorId: string;
 
    // Account id 
    public accountId: string;
 
    // Instance time [Epoch milliseconds Timestamp] 
    public birthTime: number;
 
    // Trajectory (path) of all the object instances in the scene during the time it was in the scene 
    public objectHistory: ObjectInstance[];
 
    constructor(objectId?: number, sensorId?: string, accountId?: string, birthTime?: number, objectHistory?: ObjectInstance[]) { 
        this.objectId = objectId;
        this.sensorId = sensorId;
        this.accountId = accountId;
        this.birthTime = birthTime;
        this.objectHistory = objectHistory;
    }

}


