import { StaticWatchlist } from '../messages/StaticWatchlist';

/*  
*/
export class ResponseOfStaticWatchlists {
 
    // A possible error code, if the creation was not successful. Equals zero on success 
    public code: number;
 
    // List of static watchlists 
    public watchlists: StaticWatchlist[];
 
    constructor(code?: number, watchlists?: StaticWatchlist[]) { 
        this.code = code;
        this.watchlists = watchlists;
    }

}


