import { SearchEventFindRequestBody } from '../messages/SearchEventFindRequestBody';

/*  
*/
export class SearchEventExportRequest {
 
    // Session Id 
    public sessionId: string;
 
    // 
    public body: SearchEventFindRequestBody;
 
    // Export results to a different format. Supported formats: csv | json | xml 
    public format: string;
 
    // List of fields to export (leave empty to export all fields) 
    public fields: string[];
 
    // Set download file name (without extension) 
    public fileName: string;
 
    constructor(sessionId?: string, body?: SearchEventFindRequestBody, format?: string, fields?: string[], fileName?: string) { 
        this.sessionId = sessionId;
        this.body = body;
        this.format = format;
        this.fields = fields;
        this.fileName = fileName;
    }

}


