
/* 
   SSH Information for OPEN_SSH command 
*/
export class SSHInfo {
 
    // Reverse SSH Target in the format of: ssh://<user>@<host>:<port>?TargetPort=<target_port> 
    public targetUri: string;
 
    // The private key 
    public privateKey: string;
 
    constructor(targetUri?: string, privateKey?: string) { 
        this.targetUri = targetUri;
        this.privateKey = privateKey;
    }

}


