
/*  
*/
export class ReportFindRequest {
 
    // Page number (for pagination) 
    public page: number;
 
    // Number of items per page (for pagination) 
    public pageSize: number;
 
    // Filter by report name or partial report name 
    public search: string;
 
    // Filter by the scheduled report that created job 
    public relatedScheduledReportId: string;
 
    constructor(page?: number, pageSize?: number, search?: string, relatedScheduledReportId?: string) { 
        this.page = page;
        this.pageSize = pageSize;
        this.search = search;
        this.relatedScheduledReportId = relatedScheduledReportId;
    }

}


