
/*  
*/
export class CasesServiceAddEventsRequest {
 
    // Case info Id 
    public id: string;
 
    // Search session Id (to grab the search event from) 
    public sessionId: string;
 
    // List of search event Ids to add 
    public eventId: string[];
 
    // Description to update 
    public description: string;
 
    constructor(id?: string, sessionId?: string, eventId?: string[], description?: string) { 
        this.id = id;
        this.sessionId = sessionId;
        this.eventId = eventId;
        this.description = description;
    }

}


