
/*  
*/
export class AttributeValue {
 
    // Attribute value id (or a meaningful name) 
    public id: string;
 
    // 
    public confidence: number;
 
    // Attribute value type, one of: "STRING"/"B64_STRING", "INT", "BOOL", "FLOAT", "FLOAT_ARRAY", "BOUNDING_BOX" 
    public type: string;
 
    // and just one of the following should be used, according to the Type: The value in case of a string attribute 
    public valueString: string;
 
    // The value in case of an int attribute 
    public valueInt: number;
 
    // The value in case of a boolean attribute 
    public valueBool: boolean;
 
    // The value in case of a float attribute 
    public valueFloat: number;
 
    // The value in case of array of numbers attribute (note: they are integers despite the name "FLOAT_ARRAY", Mor, 3.9.24) 
    public valueArray: number[];
 
    constructor(id?: string, confidence?: number, type?: string, valueString?: string, valueInt?: number, valueBool?: boolean, valueFloat?: number, valueArray?: number[]) { 
        this.id = id;
        this.confidence = confidence;
        this.type = type;
        this.valueString = valueString;
        this.valueInt = valueInt;
        this.valueBool = valueBool;
        this.valueFloat = valueFloat;
        this.valueArray = valueArray;
    }

}


