import { FindUserAccountRole } from '../entities/FindUserAccountRole';
import { OriginTypeCode } from '../enums/OriginTypeCode';
import { UserTypeCode } from '../enums/UserTypeCode';
import { UserStatusCode } from '../enums/UserStatusCode';

/*  
*/
export class FindUser {
 
    // User id 
    public id: string;
 
    // User name 
    public name: string;
 
    // User email 
    public email: string;
 
    // User mobile phone number (for notification and validation) 
    public mobile: string;
 
    // User type: UNDEFINED | SYSADMIN | SUPPORT | USER 
    public type: UserTypeCode;
 
    // User status: UNDEFINED | PENDING | ACTIVE | BLOCKED | SUSPENDED 
    public status: UserStatusCode;
 
    // Last Sign-in [Epoch milliseconds Timestamp] 
    public lastSignIn: number;
 
    // List of accounts and roles the user has access to 
    public accountRoles: FindUserAccountRole[];
 
    // Default account Id 
    public defaultAccount: string;
 
    // User description 
    public description: string;
 
    // The ID from the origin of creation 
    public originId: string;
 
    // User Origin type: UNDEFINED | EMAIL | LDAPGROUP | LDAPUSER 
    public originType: OriginTypeCode;
 
    constructor(id?: string, name?: string, email?: string, mobile?: string, type?: UserTypeCode, status?: UserStatusCode, lastSignIn?: number, accountRoles?: FindUserAccountRole[], defaultAccount?: string, description?: string, originId?: string, originType?: OriginTypeCode) { 
        this.id = id;
        this.name = name;
        this.email = email;
        this.mobile = mobile;
        this.type = type;
        this.status = status;
        this.lastSignIn = lastSignIn;
        this.accountRoles = accountRoles;
        this.defaultAccount = defaultAccount;
        this.description = description;
        this.originId = originId;
        this.originType = originType;
    }

}


