/* 
   Report data type code (defining what columns should be included in a report) 
*/
export enum ReportDataTypeCode {
 
    // Undefined [0] 
    UNDEFINED = 0,
 
    // Minimum amount of data, only the essentials [1] 
    ESSENTIAL = 1,
 
    // Often used data [2] 
    OFTEN_USED = 2,
 
    // All exportable data [3] 
    ALL = 3,
 
}