import { IdentifiedPoiFromImage } from '../messages/IdentifiedPoiFromImage';

/*  
*/
export class ResponseOfSearchForPoiFromImage {
 
    // A possible error code. Equals zero if the search was successful 
    public code: number;
 
    // A series of identified POIs 
    public poi: IdentifiedPoiFromImage[];
 
    constructor(code?: number, poi?: IdentifiedPoiFromImage[]) { 
        this.code = code;
        this.poi = poi;
    }

}


