
/* 
   Referenced object data for similarity search 
*/
export class ObjectData {
 
    // Dwell time in seconds, required for certain behaviors (MOVING | CROWD DENSITY | GROUPING) 
    public objectId: number;
 
    // Account Id 
    public sensorId: string;
 
    // When the referenced object was detected [Epoch milliseconds Timestamp] 
    public timestamp: number;
 
    constructor(objectId?: number, sensorId?: string, timestamp?: number) { 
        this.objectId = objectId;
        this.sensorId = sensorId;
        this.timestamp = timestamp;
    }

}


