import { StaticWatchlistWithPOIs } from '../messages/StaticWatchlistWithPOIs';

/*  
*/
export class ResponseOfStaticWatchlist {
 
    // A possible error code, if the creation was not successful. Equals zero on success 
    public code: number;
 
    // A static watchlists 
    public watchlist: StaticWatchlistWithPOIs;
 
    constructor(code?: number, watchlist?: StaticWatchlistWithPOIs) { 
        this.code = code;
        this.watchlist = watchlist;
    }

}


