
/*  
*/
export class PeopleCountingReportRequest {
 
    // Filter by sensor(s) 
    public sensorId: string[];
 
    // Filter by folder(s) 
    public folderId: string[];
 
    // Filter report after the provided timestamp (RFC3339 datetime string, URL-encoded) 
    public from: string;
 
    // Filter report before the provided timestamp (RFC3339 datetime string, URL-encoded) 
    public to: string;
 
    constructor(sensorId?: string[], folderId?: string[], from?: string, to?: string) { 
        this.sensorId = sensorId;
        this.folderId = folderId;
        this.from = from;
        this.to = to;
    }

}


