import { BehaviorTypeCode } from '../enums/BehaviorTypeCode';
import { SensorStatusCode } from '../enums/SensorStatusCode';

/*  
*/
export class AccountsServiceLogicalTreeRequest {
 
    // Filter by sensor status(es) (empty or UNDEFINED for no filter) - multiple values enabled 
    public status: SensorStatusCode[];
 
    // Filter by behavior type(s) (empty for no filter) - multiple values enabled 
    public behaviorType: BehaviorTypeCode[];
 
    // Flag to include/exclude sensors in the tree 
    public includeSensors: boolean;
 
    // Flag to include/exclude rules in the tree 
    public includeRules: boolean;
 
    // Flag to include/exclude non-recording sensors in the tree 
    public includeNonRecordingSensors: boolean;
 
    // Filter tree items by name 
    public search: string;
 
    constructor(status?: SensorStatusCode[], behaviorType?: BehaviorTypeCode[], includeSensors?: boolean, includeRules?: boolean, includeNonRecordingSensors?: boolean, search?: string) { 
        this.status = status;
        this.behaviorType = behaviorType;
        this.includeSensors = includeSensors;
        this.includeRules = includeRules;
        this.includeNonRecordingSensors = includeNonRecordingSensors;
        this.search = search;
    }

}


