
/*  
*/
export class SysExportUsageReportRequest {
 
    // Account Id 
    public id: string;
 
    // Year 
    public year: number;
 
    // Month 
    public month: number;
 
    // Export results to a different format. Supported formats: csv | json | xml 
    public format: string;
 
    // Set download file name (without extension) 
    public fileName: string;
 
    constructor(id?: string, year?: number, month?: number, format?: string, fileName?: string) { 
        this.id = id;
        this.year = year;
        this.month = month;
        this.format = format;
        this.fileName = fileName;
    }

}


