import { JobStatusCode } from '../enums/JobStatusCode';
import { BaseEntity } from '../entities/BaseEntity';

/* 
   JobStatus represents the status of an background creation job (background reports). 
*/
export class JobStatus extends BaseEntity {
 
    // Account id 
    public accountId: string;
 
    // UserId of the user who initiated this job 
    public userId: string;
 
    // Status of the job (running, error, finished, etc.) 
    public status: JobStatusCode;
 
    // Error description in case of error 
    public error: string;
 
    // The mime-type of the report to be generated 
    public mimeType: string;
 
    // destinations where result of job is sent 
    public destinationsSent: string[];
 
    // true if JobStatus created from a Scheduled job 
    public createdBySchedule: boolean;
 
    // id of scheduled report that created job 
    public relatedScheduledReportId: string;
 
    // Entity Id 
    public id: string;
 
    // Entity document type (system field) 
    public _type: string;
 
    // When the entity was created [Epoch milliseconds Timestamp] 
    public createdOn: number;
 
    // When the entity was updated [Epoch milliseconds Timestamp] 
    public updatedOn: number;
 
    // Entity path in the hierarchy 
    public path: string;
 

}


