import { POIMinimalResponse } from '../messages/POIMinimalResponse';

/*  
*/
export class StaticWatchlistWithPOIs {
 
    // System ID of the watchlist 
    public iD: string;
 
    // User given name of the watchlist 
    public name: string;
 
    // 
    public type: string;
 
    // 
    public totalPOIs: number;
 
    // 
    public pOIs: POIMinimalResponse[];
 
    constructor(iD?: string, name?: string, type?: string, totalPOIs?: number, pOIs?: POIMinimalResponse[]) { 
        this.iD = iD;
        this.name = name;
        this.type = type;
        this.totalPOIs = totalPOIs;
        this.pOIs = pOIs;
    }

}


