import { ObjectTypeCount } from '../entities/ObjectTypeCount';
import { ObjectTypeCode } from '../enums/ObjectTypeCode';
import { Point } from '../common/Point';

/* 
   Represents a single aggregated path for multiple objects 
*/
export class ObjectsPath {
 
    // Path id 
    public id: number;
 
    // Objects count 
    public count: number;
 
    // Aggregated path 
    public path: Point[];
 
    // Count per object type 
    public countPerObjectType: ObjectTypeCount[];
 
    // Parent object class type 
    public parentClass: ObjectTypeCode;
 
    constructor(id?: number, count?: number, path?: Point[], countPerObjectType?: ObjectTypeCount[], parentClass?: ObjectTypeCode) { 
        this.id = id;
        this.count = count;
        this.path = path;
        this.countPerObjectType = countPerObjectType;
        this.parentClass = parentClass;
    }

}


