
/* 
   Base entity includes common fields for all entities (persistence objects) in the system 
*/
export class BaseEntity {
 
    // Entity Id 
    public id: string;
 
    // Entity document type (system field) 
    public _type: string;
 
    // When the entity was created [Epoch milliseconds Timestamp] 
    public createdOn: number;
 
    // When the entity was updated [Epoch milliseconds Timestamp] 
    public updatedOn: number;
 
    // Entity path in the hierarchy 
    public path: string;
 
    constructor(id?: string, docType?: string, createdOn?: number, updatedOn?: number, path?: string) { 
        this.id = id;
        this._type = docType;
        this.createdOn = createdOn;
        this.updatedOn = updatedOn;
        this.path = path;
    }

}


