import { ObjectTypeCode } from '../enums/ObjectTypeCode';
import { LevelTypeCode } from '../enums/LevelTypeCode';

/*  
*/
export class SearchEventFindRequestBody {
 
    // Filter by sensor Id(s) (empty for no filter) - multiple values enabled 
    public sensorIds: string[];
 
    // Filter by object type(s) (empty for no filter) - multiple values enabled 
    public objectType: ObjectTypeCode[];
 
    // Filter by tolerance LOW | MEDIUM | HIGH 
    public tolerance: LevelTypeCode;
 
    // Filter search events after the provided timestamp (epoch milliseconds or relative time in milliseconds for negative number) 
    public from: number;
 
    // Filter search events before the provided timestamp (epoch milliseconds or relative time in milliseconds for negative number) 
    public to: number;
 
    // Sort by field and direction: (e.g. time+ = sort by time asc, id- = sort by id desc) 
    public sort: string;
 
    // Page number (for pagination) 
    public page: number;
 
    // Number of items per page (for pagination) 
    public pageSize: number;
 
    // Is prompt search flag - indicates whether to use new prompt search service or old search service 
    public isPromptSearch: boolean;
 
    constructor(sensorIds?: string[], objectType?: ObjectTypeCode[], tolerance?: LevelTypeCode, from?: number, to?: number, sort?: string, page?: number, pageSize?: number, isPromptSearch?: boolean) { 
        this.sensorIds = sensorIds;
        this.objectType = objectType;
        this.tolerance = tolerance;
        this.from = from;
        this.to = to;
        this.sort = sort;
        this.page = page;
        this.pageSize = pageSize;
        this.isPromptSearch = isPromptSearch;
    }

}


