import { ReportFormatCode } from '../enums/ReportFormatCode';
import { ReportDataTypeCode } from '../enums/ReportDataTypeCode';
import { TimeUnitCode } from '../enums/TimeUnitCode';
import { BaseEntity } from '../entities/BaseEntity';
import { ReportTypeCode } from '../enums/ReportTypeCode';

/* 
   Scheduled Report defines when to aut generate report and whet to do with it 
*/
export class ScheduledReport extends BaseEntity {
 
    // Account Id 
    public accountId: string;
 
    // report Id 
    public reportId: string;
 
    // Schedule report name 
    public name: string;
 
    // Crontab (6 space-separated fields: sec, min, hour, day, month, dayOfWeek) syntax (https://en.wikipedia.org/wiki/Cron) 
    public cron: string;
 
    // Integration target id 
    public targetId: string;
 
    // List of recipients (applicable for SMTP/EMAIL protocol) 
    public recipients: string[];
 
    // Message subject template (applicable for SMTP/EMAIL protocol) 
    public subject: string;
 
    // When the report was last sent [Epoch milliseconds Timestamp] 
    public lastSentOn: number;
 
    // UserId of the user who initiated this job 
    public userId: string;
 
    // list of folders where schedule is applied 
    public folders: string[];
 
    // list of sensors where schedule is applied 
    public sensors: string[];
 
    // Report type 
    public reportType: ReportTypeCode;
 
    // Report format 
    public reportFormat: ReportFormatCode;
 
    // Data included in report 
    public dataType: ReportDataTypeCode;
 
    // Data aggregation type 
    public aggregationType: TimeUnitCode;
 
    // Time interval of data, epoch milliseconds 
    public timeRange: number;
 
    // hour of first data to include in report, 0-23 are valid options 
    public surveillanceStartHour: number;
 
    // Entity Id 
    public id: string;
 
    // Entity document type (system field) 
    public _type: string;
 
    // When the entity was created [Epoch milliseconds Timestamp] 
    public createdOn: number;
 
    // When the entity was updated [Epoch milliseconds Timestamp] 
    public updatedOn: number;
 
    // Entity path in the hierarchy 
    public path: string;
 

}


