/* 
   Sensor anonymization code 
*/
export enum SensorAnonymizationTypeCode {
 
    // No anonymization [0] 
    NONE = 0,
 
    // Default anonymization Everything grayscale, pixelated objects [1] 
    GRAYSCALE_NONE_GRAYSCALE_ADAPTIVE = 1,
 
    // Color retained, pixelated objects [1011] 
    CHROMATIC_NONE_CHROMATIC_ADAPTIVE = 1011,
 
}