import { TimestampedCrop } from '../common/TimestampedCrop';

/* 
   List of timestamped crops with background image 
*/
export class TimestampedCropsData {
 
    // Sensor Id 
    public sensorId: string;
 
    // Background image content: encoded base64 jpeg 
    public bg: string;
 
    // List of object's timestamped crops 
    public crops: TimestampedCrop[];
 
    constructor(sensorId?: string, bg?: string, crops?: TimestampedCrop[]) { 
        this.sensorId = sensorId;
        this.bg = bg;
        this.crops = crops;
    }

}


