import { Injectable, Inject } from '@angular/core';
import { RestUtil } from '../../utils/rest-util';
import { CoreConfig } from '../../config';

import { Event } from '../entities/Event';
import { EventStatusCode } from '../enums/EventStatusCode';
import { ObjectTypeCode } from '../enums/ObjectTypeCode';
import { BehaviorTypeCode } from '../enums/BehaviorTypeCode';
import { SeverityTypeCode } from '../enums/SeverityTypeCode';
import { TimeUnitCode } from '../enums/TimeUnitCode';


/**
 * Services for events actions
 * @RequestHeader X-API-KEY The key to identify the application (portal)
 * @RequestHeader X-ACCESS-TOKEN The token to identify the logged-in user 
 */
@Injectable()
export class EventsService {

  // URL to web api
  private baseUrl = '/events';

  /**
   * Class constructor
   */
  constructor(@Inject('config') private config: CoreConfig, private rest: RestUtil) {
    this.baseUrl = this.config.api + this.baseUrl;
  }


  /**
   * Create new event
   * @Return: EntityResponse<Event>
   */
  create(body?: Event) {
    return this.rest.post(`${this.baseUrl}`, typeof body === 'object' ? JSON.stringify(body) : body);
  }

  /**
   * Get a list of integration targets applicable for this event
   * @Return: EntitiesResponse<IntegrationAction>
   */
  getIntegrations(id?: string, sensorId?: string, folderId?: string) {
    const params = new Array();		
    if (sensorId != null) { params.push(`sensorId=${sensorId}`); }
    if (folderId != null) { params.push(`folderId=${folderId}`); }

		return this.rest.get(`${this.baseUrl}/${id}/integrations`, ...params);
  }

  /**
   * Force manual event integrations
   * @Return: ActionResponse
   */
  invokeIntegrations(id?: string) {
    return this.rest.post(`${this.baseUrl}/${id}/integrations`, null);
  }

  /**
   * Change event workflow status
   * @Return: ActionResponse
   */
  changeStatus(id?: string, status?: EventStatusCode) {
    return this.rest.put(`${this.baseUrl}/${id}/status/${status}`, null);
  }

  /**
   * Change multiple events workflow status
   * @Return: ActionResponse
   */
  changeBulkStatuses(id?: string[], status?: EventStatusCode) {
    const params = new Array();		
    if (id != null) { params.push(`id=${id}`); }
    if (status != null) { params.push(`status=${status}`); }

		return this.rest.put(`${this.baseUrl}/bulk-status`, null, ...params);
  }

  /**
   * Get single event by id
   * @Return: EntityResponse<Event>
   */
  get(id?: string) {
    return this.rest.get(`${this.baseUrl}/${id}`);
  }

  /**
   * Get events by list of ids
   * @Return: EntitiesResponse<Event>
   */
  list(id?: string[]) {
    const params = new Array();		
    if (id != null) { params.push(`id=${id}`); }

		return this.rest.get(`${this.baseUrl}/list`, ...params);
  }

  /**
   * Find events by filters
   * @Return: QueryResponse<Event>
   */
  find(folderId?: string[], subFolders?: boolean, sensorId?: string[], applianceId?: string, objectType?: ObjectTypeCode[], behaviorType?: BehaviorTypeCode[], severity?: SeverityTypeCode[], status?: EventStatusCode[], rule?: string[], from?: number, to?: number, sort?: string, page?: number, pageSize?: number, after?: string, limit?: number) {
    const params = new Array();		
    if (folderId != null) { params.push(`folderId=${folderId}`); }
    if (subFolders != null) { params.push(`subFolders=${subFolders}`); }
    if (sensorId != null) { params.push(`sensorId=${sensorId}`); }
    if (applianceId != null) { params.push(`applianceId=${applianceId}`); }
    if (objectType != null) { params.push(`objectType=${objectType}`); }
    if (behaviorType != null) { params.push(`behaviorType=${behaviorType}`); }
    if (severity != null) { params.push(`severity=${severity}`); }
    if (status != null) { params.push(`status=${status}`); }
    if (rule != null) { params.push(`rule=${rule}`); }
    if (from != null) { params.push(`from=${from}`); }
    if (to != null) { params.push(`to=${to}`); }
    if (sort != null) { params.push(`sort=${sort}`); }
    if (page != null) { params.push(`page=${page}`); }
    if (pageSize != null) { params.push(`pageSize=${pageSize}`); }
    if (after != null) { params.push(`after=${after}`); }
    if (limit != null) { params.push(`limit=${limit}`); }

		return this.rest.get(`${this.baseUrl}`, ...params);
  }

  /**
   * Export list of events and filter
   * @Return: StreamContent
   */
  exportFormat(folderId?: string[], subFolders?: boolean, sensorId?: string[], objectType?: ObjectTypeCode[], behaviorType?: BehaviorTypeCode[], severity?: SeverityTypeCode[], status?: EventStatusCode[], rule?: string[], from?: number, to?: number, sort?: string, format?: string, fields?: string[], fileName?: string) {
    const params = new Array();		
    if (folderId != null) { params.push(`folderId=${folderId}`); }
    if (subFolders != null) { params.push(`subFolders=${subFolders}`); }
    if (sensorId != null) { params.push(`sensorId=${sensorId}`); }
    if (objectType != null) { params.push(`objectType=${objectType}`); }
    if (behaviorType != null) { params.push(`behaviorType=${behaviorType}`); }
    if (severity != null) { params.push(`severity=${severity}`); }
    if (status != null) { params.push(`status=${status}`); }
    if (rule != null) { params.push(`rule=${rule}`); }
    if (from != null) { params.push(`from=${from}`); }
    if (to != null) { params.push(`to=${to}`); }
    if (sort != null) { params.push(`sort=${sort}`); }
    if (format != null) { params.push(`format=${format}`); }
    if (fields != null) { params.push(`fields=${fields}`); }
    if (fileName != null) { params.push(`fileName=${fileName}`); }

		return this.rest.download(`events`,`${this.baseUrl}/export`, ...params);
  }

  /**
   * Get event image [response content type: image/jpeg]
   * @Return: StreamingOutput of the image
   */
  getEventImage(id?: string) {
    return this.rest.download(`events`,`${this.baseUrl}/${id}/image`);
  }

  /**
   * Get event clip [response content type: video/mp4]
   * @Return: StreamingOutput of the clip
   */
  getEventClip(id?: string) {
    return this.rest.download(`events`,`${this.baseUrl}/${id}/clip`);
  }

  /**
   * Get events count overtime for all events in the system
   * @Return: EntityResponse<EventCountTimeSeries>
   */
  getEventsCountOvertime(folderId?: string, sensorId?: string, objectType?: ObjectTypeCode[], behaviorType?: BehaviorTypeCode[], severity?: SeverityTypeCode[], status?: EventStatusCode[], rule?: string[], from?: number, to?: number, interval?: TimeUnitCode, labelFormat?: string) {
    const params = new Array();		
    if (folderId != null) { params.push(`folderId=${folderId}`); }
    if (sensorId != null) { params.push(`sensorId=${sensorId}`); }
    if (objectType != null) { params.push(`objectType=${objectType}`); }
    if (behaviorType != null) { params.push(`behaviorType=${behaviorType}`); }
    if (severity != null) { params.push(`severity=${severity}`); }
    if (status != null) { params.push(`status=${status}`); }
    if (rule != null) { params.push(`rule=${rule}`); }
    if (from != null) { params.push(`from=${from}`); }
    if (to != null) { params.push(`to=${to}`); }
    if (interval != null) { params.push(`interval=${interval}`); }
    if (labelFormat != null) { params.push(`labelFormat=${labelFormat}`); }

		return this.rest.get(`${this.baseUrl}/overtime`, ...params);
  }

  /**
   * Get events distribution by status
   * @Return: EntityResponse<DistributionOfLong>
   */
  getEventsCountByStatus(folderId?: string, sensorId?: string, objectType?: ObjectTypeCode[], behaviorType?: BehaviorTypeCode[], severity?: SeverityTypeCode[], rule?: string[], from?: number, to?: number) {
    const params = new Array();		
    if (folderId != null) { params.push(`folderId=${folderId}`); }
    if (sensorId != null) { params.push(`sensorId=${sensorId}`); }
    if (objectType != null) { params.push(`objectType=${objectType}`); }
    if (behaviorType != null) { params.push(`behaviorType=${behaviorType}`); }
    if (severity != null) { params.push(`severity=${severity}`); }
    if (rule != null) { params.push(`rule=${rule}`); }
    if (from != null) { params.push(`from=${from}`); }
    if (to != null) { params.push(`to=${to}`); }

		return this.rest.get(`${this.baseUrl}/count/by-status`, ...params);
  }

  /**
   * Get events distribution by object type
   * @Return: EntityResponse<DistributionOfLong>
   */
  getEventsCountByObject(folderId?: string, sensorId?: string, behaviorType?: BehaviorTypeCode[], severity?: SeverityTypeCode[], status?: EventStatusCode[], rule?: string[], from?: number, to?: number) {
    const params = new Array();		
    if (folderId != null) { params.push(`folderId=${folderId}`); }
    if (sensorId != null) { params.push(`sensorId=${sensorId}`); }
    if (behaviorType != null) { params.push(`behaviorType=${behaviorType}`); }
    if (severity != null) { params.push(`severity=${severity}`); }
    if (status != null) { params.push(`status=${status}`); }
    if (rule != null) { params.push(`rule=${rule}`); }
    if (from != null) { params.push(`from=${from}`); }
    if (to != null) { params.push(`to=${to}`); }

		return this.rest.get(`${this.baseUrl}/count/by-object`, ...params);
  }

  /**
   * Get events distribution by behavior
   * @Return: EntityResponse<DistributionOfLong>
   */
  getEventsCountByBehavior(folderId?: string, sensorId?: string, objectType?: ObjectTypeCode[], severity?: SeverityTypeCode[], status?: EventStatusCode[], rule?: string[], from?: number, to?: number) {
    const params = new Array();		
    if (folderId != null) { params.push(`folderId=${folderId}`); }
    if (sensorId != null) { params.push(`sensorId=${sensorId}`); }
    if (objectType != null) { params.push(`objectType=${objectType}`); }
    if (severity != null) { params.push(`severity=${severity}`); }
    if (status != null) { params.push(`status=${status}`); }
    if (rule != null) { params.push(`rule=${rule}`); }
    if (from != null) { params.push(`from=${from}`); }
    if (to != null) { params.push(`to=${to}`); }

		return this.rest.get(`${this.baseUrl}/count/by-behavior`, ...params);
  }

  /**
   * Direct link to download event image [response content type: image/jpeg]
   * This link is injected dynamically by the system to the ImagePath property of the event, the link includes time-limited token (valid for 12 hours) to identify the account and event
   * @Return: StreamingOutput of the image
   */
  downloadEventImage(imageToken?: string) {
    const params = new Array();		
    if (imageToken != null) { params.push(`imageToken=${imageToken}`); }

		return this.rest.download(`events`,`${this.baseUrl}Image`, ...params);
  }

  /**
   * Direct link to download event clip [response content type: video/mp4]
   * This link is injected dynamically by the system to the ClipPath property of the event, the link includes time-limited token (valid for 12 hours) to identify the account and event
   * @Return: StreamingOutput of the clip
   */
  downloadEventClip(imageToken?: string) {
    const params = new Array();		
    if (imageToken != null) { params.push(`imageToken=${imageToken}`); }

		return this.rest.download(`events`,`${this.baseUrl}Clip`, ...params);
  }

}
