import { ObjectTypeCode } from '../enums/ObjectTypeCode';
import { SeverityTypeCode } from '../enums/SeverityTypeCode';
import { Sensitivity } from '../common/Sensitivity';
import { RulePolygon } from '../common/RulePolygon';
import { RuleAutoSensitivityCode } from '../enums/RuleAutoSensitivityCode';
import { BaseEntity } from '../entities/BaseEntity';
import { BehaviorTypeCode } from '../enums/BehaviorTypeCode';

/* 
   Rule Represents real-time analytic rule, the structure is a union of all the attributes used by all the types of
   real-time rules (crossing, moving, grouping ...) hence not all attributes are used for every rule type 
*/
export class Rule extends BaseEntity {
 
    // Account Id 
    public accountId: string;
 
    // Sensor Id 
    public sensorId: string;
 
    // Rule name 
    public name: string;
 
    // Rule description 
    public description: string;
 
    // Schedule Id 
    public scheduleId: string;
 
    // Indicate if the rule is explicitly enabled or disabled (by user or by API call - e.g. arm/disarm) 
    public enabled: boolean;
 
    // Indicate if the rule is active or inactive due to it's schedule / calendar time slots 
    public active: boolean;
 
    // Rule external id (how the rule is identified in other system) 
    public externalId: string;
 
    // Rule behavior type: MOVING | CROSSING | CROWD DENSITY ... 
    public behaviorType: BehaviorTypeCode;
 
    // Rule object types: PERSON | VEHICLE | MOTORCYCLE ... 
    public objectTypes: ObjectTypeCode[];
 
    // Default severity level for this rule: UNDEFINED | LOW | MEDIUM | HIGH | CRITICAL 
    public severity: SeverityTypeCode;
 
    // Sensitivity (for Anomaly rule only) 
    public sensitivity: Sensitivity;
 
    // Dwell time [seconds] 
    public dwellTime: number;
 
    // An optional speed filter to the moving in an area and line crossing rules [KPH] 
    public minSpeed: number;
 
    // Max number of people in group (for Crowd density rule) 
    public maxObjectsInGroup: number;
 
    // Max distance between people to be considered as group (for Grouping rule) 
    public clusterDistance: number;
 
    // List of polygons 
    public polygons: RulePolygon[];
 
    // Base64 encoded cropped asset image 
    public referenceCrop: string;
 
    // Flag to indicate if event image should be generated 
    public createImage: boolean;
 
    // Flag to indicate if event clip should be generated 
    public createClip: boolean;
 
    // When was the last anomaly training (for Anomaly rule only) [Epoch milliseconds Timestamp] 
    public lastTrainingOn: number;
 
    // When will be the next anomaly training (for Anomaly rule only) [Epoch milliseconds Timestamp] 
    public nextTrainingOn: number;
 
    // The percent of required metadata that was collected (0-100) 
    public trainingReadiness: number;
 
    // External model Id used by the rule 
    public externalModel: string;
 
    // Excluded object types for this rule 
    public excludeObjectTypes: ObjectTypeCode[];
 
    // Rule's auto sensitivity 
    public autoSensitivity: RuleAutoSensitivityCode;
 
    // Used in conjunction with WatchListId. Set to true to trigger rule when object is detected in any WatchListId. 
    public existsInWatchlist: boolean;
 
    // List of external object lists, used for filtering when events should be triggered. 
    public watchlistId: string[];
 
    // Entity Id 
    public id: string;
 
    // Entity document type (system field) 
    public _type: string;
 
    // When the entity was created [Epoch milliseconds Timestamp] 
    public createdOn: number;
 
    // When the entity was updated [Epoch milliseconds Timestamp] 
    public updatedOn: number;
 
    // Entity path in the hierarchy 
    public path: string;
 

}


