import { Sensitivity } from '../common/Sensitivity';

/* 
   Rule default encapsulates boundaries and default values for the Sensitivity Rule parameter 
*/
export class RuleDefaultSensitivity {
 
    // Min value (Lower bound) 
    public min: Sensitivity;
 
    // Max value (Upper bound) 
    public max: Sensitivity;
 
    // Default value 
    public defaultValue: Sensitivity;
 
    constructor(min?: Sensitivity, max?: Sensitivity, defaultValue?: Sensitivity) { 
        this.min = min;
        this.max = max;
        this.defaultValue = defaultValue;
    }

}


