
/*  
*/
export class SensorHeatmapEventsRequest {
 
    // Search session Id 
    public sessionId: string;
 
    // Sensor Id 
    public sensorId: string;
 
    // Heatmap cell row number 
    public row: number;
 
    // Heatmap cell column number 
    public col: number;
 
    // Page number (for pagination) 
    public page: number;
 
    // Number of items per page (for pagination) 
    public pageSize: number;
 
    constructor(sessionId?: string, sensorId?: string, row?: number, col?: number, page?: number, pageSize?: number) { 
        this.sessionId = sessionId;
        this.sensorId = sensorId;
        this.row = row;
        this.col = col;
        this.page = page;
        this.pageSize = pageSize;
    }

}


