import { ObjectTypeCode } from '../enums/ObjectTypeCode';

/* 
   Represents object type count distribution 
*/
export class ObjectTypeCount {
 
    // Object type 
    public objectType: ObjectTypeCode;
 
    // Count per object type 
    public count: number;
 
    constructor(objectType?: ObjectTypeCode, count?: number) { 
        this.objectType = objectType;
        this.count = count;
    }

}


