import { BaseEntity } from '../entities/BaseEntity';

/* 
   Sensor usage type describes the total time (in minutes) per day that the sensor was active (including warning and error) for billing 
*/
export class SensorUsage extends BaseEntity {
 
    // Account Id 
    public accountId: string;
 
    // Sensor Id 
    public sensorId: string;
 
    // Day Id is the usage day as number in the format of: YYYYMMDD 
    public dayId: number;
 
    // Total time in minutes the sensor was active during this day 
    public minutes: number;
 
    // Entity Id 
    public id: string;
 
    // Entity document type (system field) 
    public _type: string;
 
    // When the entity was created [Epoch milliseconds Timestamp] 
    public createdOn: number;
 
    // When the entity was updated [Epoch milliseconds Timestamp] 
    public updatedOn: number;
 
    // Entity path in the hierarchy 
    public path: string;
 

}


