import { SensorUsageReport } from '../common/SensorUsageReport';
import { BaseEntity } from '../entities/BaseEntity';
import { TimeFrame } from '../common/TimeFrame';

/* 
   Sensor usage report type for billing 
*/
export class UsageReport extends BaseEntity {
 
    // Account Id 
    public accountId: string;
 
    // Report period 
    public period: TimeFrame;
 
    // Total billable sensors for the report period 
    public totalSensors: number;
 
    // List of ( key, minutes, isBillable ) 
    public usage: SensorUsageReport[];
 
    // Pointer to the current item, for pagination of the response. 
    public cursor: string;
 
    // Entity Id 
    public id: string;
 
    // Entity document type (system field) 
    public _type: string;
 
    // When the entity was created [Epoch milliseconds Timestamp] 
    public createdOn: number;
 
    // When the entity was updated [Epoch milliseconds Timestamp] 
    public updatedOn: number;
 
    // Entity path in the hierarchy 
    public path: string;
 

}


