import { AccountRole } from '../common/AccountRole';
import { Permission } from '../common/Permission';
import { UserTypeCode } from '../enums/UserTypeCode';
import { UserStatusCode } from '../enums/UserStatusCode';
import { PlatformTypeCode } from '../enums/PlatformTypeCode';

/* 
   Login data (returned by the API after successful login) 
*/
export class LoginData {
 
    // Access token to use (for the default account) 
    public accessToken: string;
 
    // Current account id and role 
    public accountRole: AccountRole;
 
    // User permissions 
    public permissions: Permission[];
 
    // User Id 
    public userId: string;
 
    // User name 
    public userName: string;
 
    // User email 
    public userEmail: string;
 
    // User type 
    public userType: UserTypeCode;
 
    // User status 
    public userStatus: UserStatusCode;
 
    // User must change password on next login 
    public changePassword: boolean;
 
    // User accepted the EULA agreement 
    public acceptedEula: boolean;
 
    // User accepted the Privacy Compliance agreement 
    public acceptedPrivacyCompliance: boolean;
 
    // Platform type for current account 
    public platformType: PlatformTypeCode;
 
    constructor(accessToken?: string, accountRole?: AccountRole, permissions?: Permission[], userId?: string, userName?: string, userEmail?: string, userType?: UserTypeCode, userStatus?: UserStatusCode, changePassword?: boolean, acceptedEula?: boolean, acceptedPrivacyCompliance?: boolean, platformType?: PlatformTypeCode) { 
        this.accessToken = accessToken;
        this.accountRole = accountRole;
        this.permissions = permissions;
        this.userId = userId;
        this.userName = userName;
        this.userEmail = userEmail;
        this.userType = userType;
        this.userStatus = userStatus;
        this.changePassword = changePassword;
        this.acceptedEula = acceptedEula;
        this.acceptedPrivacyCompliance = acceptedPrivacyCompliance;
        this.platformType = platformType;
    }

}


