import { SocketOpCode } from '../enums/SocketOpCode';
import { SocketEntityChangePayload } from '../sockets/SocketEntityChangePayload';

/* 
   @WebSocketMessage
   This message is pushed to the client upon any new entity change in the registered account 
*/
export class SocketEntityChangeNotification {
 
    // Message OpCode (1031) 
    public op: SocketOpCode;
 
    // Message payload 
    public payload: SocketEntityChangePayload;
 
    constructor(op?: SocketOpCode, payload?: SocketEntityChangePayload) { 
        this.op = op;
        this.payload = payload;
    }

}


