export class SysSystemBackupRequest {
 
    // Filter list of items (users | config | <accountId>...) 
    public filter: string[];
 
    // Set download file name (without extension) 
    public fileName: string;
 
    constructor(filter?: string[], fileName?: string) { 
        this.filter = filter;
        this.fileName = fileName;
    }

}


