import { Injectable, Inject } from '@angular/core';
import { RestUtil } from '../../utils/rest-util';
import { CoreConfig } from '../../config';

import { DetectionModel } from '../entities/DetectionModel';


/**
 * List of all detection models related actions for system administrator only
 * @RequestHeader X-API-KEY The key to identify the application (portal)
 * @RequestHeader X-ACCESS-TOKEN The token to identify the logged-in user 
 */
@Injectable()
export class SysModelsService {

  // URL to web api
  private baseUrl = '/sys/models';

  /**
   * Class constructor
   */
  constructor(@Inject('config') private config: CoreConfig, private rest: RestUtil) {
    this.baseUrl = this.config.api + this.baseUrl;
  }


  /**
   * Create new Detection Model
   * @Return: EntityResponse<DetectionModel>
   */
  create(body?: DetectionModel) {
    return this.rest.post(`${this.baseUrl}`, typeof body === 'object' ? JSON.stringify(body) : body);
  }

  /**
   * Update new Detection Model
   * @Return: EntityResponse<DetectionModel>
   */
  update(body?: DetectionModel) {
    return this.rest.put(`${this.baseUrl}`, typeof body === 'object' ? JSON.stringify(body) : body);
  }

  /**
   * Delete Detection Model
   * @Return: ActionResponse
   */
  delete(id?: string) {
    return this.rest.delete(`${this.baseUrl}/${id}`);
  }

  /**
   * Get single Detection Model by id
   * @Return: EntityResponse<DetectionModel>
   */
  get(id?: string) {
    return this.rest.get(`${this.baseUrl}/${id}`);
  }

  /**
   * Get list of all detection models
   * @Return: EntitiesResponse<DetectionModel>
   */
  find() {
    return this.rest.get(`${this.baseUrl}`);
  }

  /**
   * Get list of all detection models per sensor
   * @Return: EntitiesResponse<DetectionModel>
   */
  getSensorModels(sensorId?: string) {
    return this.rest.get(`${this.baseUrl}/sensors/${sensorId}`);
  }

  /**
   * Add detection model to the list of detection models for the given sensor
   * @Return: ActionResponse
   */
  addSensorModels(sensorId?: string, id?: string[]) {
    const params = new Array();		
    if (id != null) { params.push(`id=${id}`); }

		return this.rest.post(`${this.baseUrl}/sensors/${sensorId}`, null, ...params);
  }

  /**
   * Update the list of detection model for the given sensor
   * @Return: ActionResponse
   */
  updateSensorModels(sensorId?: string, id?: string[]) {
    const params = new Array();		
    if (id != null) { params.push(`id=${id}`); }

		return this.rest.put(`${this.baseUrl}/sensors/${sensorId}`, null, ...params);
  }

  /**
   * Remove detection model from sensor
   * @Return: ActionResponse
   */
  removeSensorModels(sensorId?: string, id?: string[]) {
    const params = new Array();		
    if (id != null) { params.push(`id=${id}`); }

		return this.rest.delete(`${this.baseUrl}/sensors/${sensorId}`, ...params);
  }

}
