import { TimeSlot } from '../common/TimeSlot';

/* 
   Timeline (for sensor rule scheduling) 
*/
export class Timeline {
 
    // Start time [Epoch milliseconds Timestamp] 
    public from: number;
 
    // End time [Epoch milliseconds Timestamp] 
    public to: number;
 
    // List of time slots 
    public slots: TimeSlot[];
 
    constructor(from?: number, to?: number, slots?: TimeSlot[]) { 
        this.from = from;
        this.to = to;
        this.slots = slots;
    }

}


