import { ObjectTypeCode } from '../enums/ObjectTypeCode';
import { RuleDefault } from '../common/RuleDefault';
import { ObjectTypeNode } from '../common/ObjectTypeNode';
import { SensorTypeCode } from '../enums/SensorTypeCode';
import { ViewTypeCode } from '../enums/ViewTypeCode';
import { RuleAutoSensitivityCode } from '../enums/RuleAutoSensitivityCode';
import { RuleDefaultSensitivity } from '../common/RuleDefaultSensitivity';
import { BehaviorTypeCode } from '../enums/BehaviorTypeCode';

/* 
   Rule specification describe rule parameters 
*/
export class RuleSpec {
 
    // Behavior types for this rule 
    public behaviorType: BehaviorTypeCode;
 
    // Rule type name 
    public ruleTypeName: string;
 
    // Available object types for this rule 
    public objectTypes: ObjectTypeCode[];
 
    // Is this rule requires line drawing (true) or area drawing (false) 
    public isLineDrawing: boolean;
 
    // Dwell Time 
    public dwellTime: RuleDefault;
 
    // Min Speed filter (moving in an area, line crossing) 
    public minSpeed: RuleDefault;
 
    // Number of people In Group (for grouping and crowd density rules) 
    public peopleInGroup: RuleDefault;
 
    // Max distance between people to be considered as group (for Grouping rule) 
    public clusterDistance: RuleDefault;
 
    // Is reference crop required (for Asset Protection) 
    public referenceCrop: boolean;
 
    // Available object types for this rule organized in hierarchy 
    public objectHierarchy: ObjectTypeNode[];
 
    // Limit this rule on specific sensor types 
    public sensorTypes: SensorTypeCode[];
 
    // External model Id used by the rule 
    public externalModel: string;
 
    // Limit this rule on specific view types 
    public viewTypes: ViewTypeCode[];
 
    // Excluded object types for this rule 
    public excludeObjectTypes: ObjectTypeCode[];
 
    // Rule's auto sensitivity 
    public autoSensitivity: RuleAutoSensitivityCode[];
 
    // Sensitivity (for Anomaly rule only), defines the expected number of events per day 
    public sensitivity: RuleDefaultSensitivity;
 
    // Used in conjuction with WatchListId to indicate if this rule should trigger if the object is found in the watchlist or not. Added for MHA 
    public existsInWatchlist: boolean[];
 
    constructor(behaviorType?: BehaviorTypeCode, ruleTypeName?: string, objectTypes?: ObjectTypeCode[], isLineDrawing?: boolean, dwellTime?: RuleDefault, minSpeed?: RuleDefault, peopleInGroup?: RuleDefault, clusterDistance?: RuleDefault, referenceCrop?: boolean, objectHierarchy?: ObjectTypeNode[], sensorTypes?: SensorTypeCode[], externalModel?: string, viewTypes?: ViewTypeCode[], excludeObjectTypes?: ObjectTypeCode[], autoSensitivity?: RuleAutoSensitivityCode[], sensitivity?: RuleDefaultSensitivity, existsInWatchlist?: boolean[]) { 
        this.behaviorType = behaviorType;
        this.ruleTypeName = ruleTypeName;
        this.objectTypes = objectTypes;
        this.isLineDrawing = isLineDrawing;
        this.dwellTime = dwellTime;
        this.minSpeed = minSpeed;
        this.peopleInGroup = peopleInGroup;
        this.clusterDistance = clusterDistance;
        this.referenceCrop = referenceCrop;
        this.objectHierarchy = objectHierarchy;
        this.sensorTypes = sensorTypes;
        this.externalModel = externalModel;
        this.viewTypes = viewTypes;
        this.excludeObjectTypes = excludeObjectTypes;
        this.autoSensitivity = autoSensitivity;
        this.sensitivity = sensitivity;
        this.existsInWatchlist = existsInWatchlist;
    }

}


