import { EntityTypeCode } from '../enums/EntityTypeCode';
import { SensorStatusCode } from '../enums/SensorStatusCode';
import { TimeFrame } from '../common/TimeFrame';
import { BehaviorTypeCode } from '../enums/BehaviorTypeCode';
import { PermissionMask } from '../enums/PermissionMask';

/* 
   Tree item for account hierarchy tree 
*/
export class TreeItem {
 
    // Item Id 
    public itemId: string;
 
    // Tree item entity type 
    public type: EntityTypeCode;
 
    // Tree item name 
    public name: string;
 
    // Item type name (ACCOUNT | FOLDER | APPLIANCE | SENSOR | RULE) 
    public typeName: string;
 
    // Sensor status: UNDEFINED | ACTIVE | WARNING | ERROR | INACTIVE | SUSPENDED | PENDING (specific to SENSOR) 
    public status: SensorStatusCode;
 
    // Inactive analytics for all sensors for the specified time frame (specific to FOLDER) 
    public shunt: TimeFrame;
 
    // Schedule id (specific to RULE) 
    public scheduleId: string;
 
    // Indicate if the item is active or inactive (specific to RULE) 
    public active: boolean;
 
    // Behavior type code (specific to RULE) 
    public behaviorType: BehaviorTypeCode;
 
    // Item permissions for the calling user 
    public permissions: PermissionMask;
 
    constructor(itemId?: string, type?: EntityTypeCode, name?: string, typeName?: string, status?: SensorStatusCode, shunt?: TimeFrame, scheduleId?: string, active?: boolean, behaviorType?: BehaviorTypeCode, permissions?: PermissionMask) { 
        this.itemId = itemId;
        this.type = type;
        this.name = name;
        this.typeName = typeName;
        this.status = status;
        this.shunt = shunt;
        this.scheduleId = scheduleId;
        this.active = active;
        this.behaviorType = behaviorType;
        this.permissions = permissions;
    }

}


