import { SocketOpCode } from '../enums/SocketOpCode';

/* 
   @WebSocketMessage
   This message is sent from the client to send the command in the payload to the remote message 
*/
export class SSHSocketInputRequest {
 
    // Message OpCode (1103) 
    public op: SocketOpCode;
 
    // Payload is the command to send to the remote client 
    public payload: string;
 
    constructor(op?: SocketOpCode, payload?: string) { 
        this.op = op;
        this.payload = payload;
    }

}


