
/*  
*/
export class SensorsFetchObjectsCropsRequest {
 
    // Sensor Id 
    public id: string;
 
    // List of timestamps (epoch time milliseconds) 
    public ts: number[];
 
    // List of object Ids 
    public objectId: number[];
 
    // Name of the folder only (in the pre-configured bucket) to store the cropped images 
    public bucketFolder: string;
 
    constructor(id?: string, ts?: number[], objectId?: number[], bucketFolder?: string) { 
        this.id = id;
        this.ts = ts;
        this.objectId = objectId;
        this.bucketFolder = bucketFolder;
    }

}


