import { BehaviorTypeCode } from '../enums/BehaviorTypeCode';
import { SeverityTypeCode } from '../enums/SeverityTypeCode';
import { EventStatusCode } from '../enums/EventStatusCode';

/*  
*/
export class EventsCountByObjectRequest {
 
    // Filter by folder (empty for no filter) 
    public folderId: string;
 
    // Filter by sensor Id (empty for no filter) 
    public sensorId: string;
 
    // Filter by behavior type(s) (empty for no filter) 
    public behaviorType: BehaviorTypeCode[];
 
    // Filter by severity(ies) (empty for no filter) 
    public severity: SeverityTypeCode[];
 
    // Filter by event status(es) (empty for no filter) - multiple values enabled 
    public status: EventStatusCode[];
 
    // Filter by event rule(s) (empty for no filter) - multiple values enabled 
    public rule: string[];
 
    // Filter events after the provided timestamp (epoch milliseconds or relative time in milliseconds for negative number) 
    public from: number;
 
    // Filter events before the provided timestamp (epoch milliseconds or relative time in milliseconds for negative number) 
    public to: number;
 
    constructor(folderId?: string, sensorId?: string, behaviorType?: BehaviorTypeCode[], severity?: SeverityTypeCode[], status?: EventStatusCode[], rule?: string[], from?: number, to?: number) { 
        this.folderId = folderId;
        this.sensorId = sensorId;
        this.behaviorType = behaviorType;
        this.severity = severity;
        this.status = status;
        this.rule = rule;
        this.from = from;
        this.to = to;
    }

}


