
/*  
*/
export class UsageReportRequest {
 
    // Year 
    public year: number;
 
    // Month 
    public month: number;
 
    // After or "Cursor" 
    public after: string;
 
    // Limit or "PageSize" 
    public limit: number;
 
    constructor(year?: number, month?: number, after?: string, limit?: number) { 
        this.year = year;
        this.month = month;
        this.after = after;
        this.limit = limit;
    }

}


