import { BaseEntity } from '../entities/BaseEntity';

/* 
   Point of Interest (POI) type 
*/
export class Poi extends BaseEntity {
 
    // Sensor Id 
    public sensorId: string;
 
    // Object Id (stored as string in Elasticsearch) 
    public objectId: string;
 
    // Point of Interest identifier 
    public poi: string;
 
    // Event timestamp in UTC [Epoch milliseconds Timestamp] 
    public timestamp: number;
 
    // Entity Id 
    public id: string;
 
    // Entity document type (system field) 
    public _type: string;
 
    // When the entity was created [Epoch milliseconds Timestamp] 
    public createdOn: number;
 
    // When the entity was updated [Epoch milliseconds Timestamp] 
    public updatedOn: number;
 
    // Entity path in the hierarchy 
    public path: string;
 

}


