import { EntityTypeCode } from '../enums/EntityTypeCode';

/*  
*/
export class IntegrationStatusFindRequest {
 
    // Filter by source (can be only EVENT or HEALTH_EVENT) 
    public source: EntityTypeCode;
 
    // Filter by entity id 
    public entityId: string;
 
    // Filter by integration target id 
    public integrationTargetId: string;
 
    // Filter by integration action id 
    public integrationActionId: string;
 
    // Filter integration status after the provided timestamp (epoch milliseconds or relative time in milliseconds for negative number) 
    public from: number;
 
    // Filter integration status before the provided timestamp (epoch milliseconds or relative time in milliseconds for negative number) 
    public to: number;
 
    // Sort by field and direction: (e.g. createdOn+ = sort by creation time asc, id- = sort by id desc) 
    public sort: string;
 
    // If ResolveNames is true, then the names of integrations will be resolved in the response 
    public resolveNames: boolean;
 
    // Page number (for pagination) 
    public page: number;
 
    // Number of items per page (for pagination) 
    public pageSize: number;
 
    constructor(source?: EntityTypeCode, entityId?: string, integrationTargetId?: string, integrationActionId?: string, from?: number, to?: number, sort?: string, resolveNames?: boolean, page?: number, pageSize?: number) { 
        this.source = source;
        this.entityId = entityId;
        this.integrationTargetId = integrationTargetId;
        this.integrationActionId = integrationActionId;
        this.from = from;
        this.to = to;
        this.sort = sort;
        this.resolveNames = resolveNames;
        this.page = page;
        this.pageSize = pageSize;
    }

}


