
/*  
*/
export class ResponsePoi {
 
    // 
    public poiId: string;
 
    // 
    public name: string;
 
    // 
    public base64EncodedImage: string;
 
    // 
    public watchlistIds: string[];
 
    // 
    public expireTime: number;
 
    constructor(poiId?: string, name?: string, base64EncodedImage?: string, watchlistIds?: string[], expireTime?: number) { 
        this.poiId = poiId;
        this.name = name;
        this.base64EncodedImage = base64EncodedImage;
        this.watchlistIds = watchlistIds;
        this.expireTime = expireTime;
    }

}


