import { SocketOpCode } from '../enums/SocketOpCode';
import { BaseEntity } from '../entities/BaseEntity';

/* 
   @WebSocketMessage
   Payload for entity change notification socket message 
*/
export class SocketEntityChangePayload {
 
    // Operation code (1:add, 2:update, 3:delete) 
    public op: SocketOpCode;
 
    // Entity Identifier in the format: entityType::entityId 
    public sid: string;
 
    // Entity data 
    public entity: BaseEntity;
 
    constructor(op?: SocketOpCode, sid?: string, entity?: BaseEntity) { 
        this.op = op;
        this.sid = sid;
        this.entity = entity;
    }

}


