import { TimeFrame } from '../common/TimeFrame';
import { ObjectTypeCode } from '../enums/ObjectTypeCode';
import { HeatmapRow } from '../entities/HeatmapRow';
import { BaseEntity } from '../entities/BaseEntity';

/* 
   Group of object counts in cells of the sensor's field of view 
*/
export class SensorHeatmap extends BaseEntity {
 
    // Session Id 
    public sessionId: string;
 
    // Sensor Id 
    public sensorId: string;
 
    // Paths query time frame: from-to [Epoch milliseconds Timestamps] 
    public timeFrame: TimeFrame;
 
    // Paths query object type list 
    public objectType: ObjectTypeCode[];
 
    // Number of cells in the x and y dimensions 
    public dim: number;
 
    // Object count per cell 
    public heatmap: HeatmapRow[];
 
    // Entity Id 
    public id: string;
 
    // Entity document type (system field) 
    public _type: string;
 
    // When the entity was created [Epoch milliseconds Timestamp] 
    public createdOn: number;
 
    // When the entity was updated [Epoch milliseconds Timestamp] 
    public updatedOn: number;
 
    // Entity path in the hierarchy 
    public path: string;
 

}


