
/* 
   Time slot (for rule scheduling) 
*/
export class TimeSlot {
 
    // Start time [Epoch milliseconds Timestamp] 
    public from: number;
 
    // End time [Epoch milliseconds Timestamp] 
    public to: number;
 
    // Active slot 
    public inactive: boolean;
 
    constructor(from?: number, to?: number, inactive?: boolean) { 
        this.from = from;
        this.to = to;
        this.inactive = inactive;
    }

}


