export class PoiBoundingBox {
 
    // 
    public maxX: number;
 
    // 
    public maxY: number;
 
    // 
    public minX: number;
 
    // 
    public minY: number;
 
    constructor(maxX?: number, maxY?: number, minX?: number, minY?: number) { 
        this.maxX = maxX;
        this.maxY = maxY;
        this.minX = minX;
        this.minY = minY;
    }

}


