import { Injectable, Inject } from '@angular/core';
import { RestUtil } from '../../utils/rest-util';
import { CoreConfig } from '../../config';

import { MemberRegistration } from '../common/MemberRegistration';
import { ServiceAccountRegistration } from '../common/ServiceAccountRegistration';
import { Member } from '../entities/Member';
import { UserTypeCode } from '../enums/UserTypeCode';
import { UserStatusCode } from '../enums/UserStatusCode';
import { UserInvitation } from '../common/UserInvitation';


/**
 * List of all account members related actions for account administrator only 
 */
@Injectable()
export class MembersService {

  // URL to web api
  private baseUrl = '/members';

  /**
   * Class constructor
   */
  constructor(@Inject('config') private config: CoreConfig, private rest: RestUtil) {
    this.baseUrl = this.config.api + this.baseUrl;
  }


  /**
   * Send invitation to a new member for the current account
   * @Return: ActionResponse
   */
  invite(body?: UserInvitation) {
    return this.rest.post(`${this.baseUrl}/invite`, typeof body === 'object' ? JSON.stringify(body) : body);
  }

  /**
   * Register user for the account, it a user already exists, an invitation to the new account will be sent
   * @Return: EntityResponse<Member>
   */
  register(body?: MemberRegistration) {
    return this.rest.post(`${this.baseUrl}/register`, typeof body === 'object' ? JSON.stringify(body) : body);
  }

  /**
   * Create service account
   * @Return: ActionResponse
   */
  registerServiceAccount(body?: ServiceAccountRegistration) {
    return this.rest.post(`${this.baseUrl}/service-account`, typeof body === 'object' ? JSON.stringify(body) : body);
  }

  /**
   * Resend invitation to an existing user for the current account
   * @Return: ActionResponse
   */
  reInvite(id?: string) {
    return this.rest.post(`${this.baseUrl}/re-invite/${id}`, null);
  }

  /**
   * Update member
   * @Return: EntityResponse<Member>
   */
  update(id?: string, body?: Member) {
    return this.rest.put(`${this.baseUrl}/${id}`, typeof body === 'object' ? JSON.stringify(body) : body);
  }

  /**
   * Delete member from the account
   * The user will be removed from the account, if no accounts associated with the member, it will be deleted
   * @Return: ActionResponse
   */
  delete(id?: string) {
    return this.rest.delete(`${this.baseUrl}/${id}`);
  }

  /**
   * Get single member by id
   * @Return: EntityResponse<Member>
   */
  get(id?: string) {
    return this.rest.get(`${this.baseUrl}/${id}`);
  }

  /**
   * Get single member by email
   * @Return: EntityResponse<Member>
   */
  getByEmail(email?: string) {
    return this.rest.get(`${this.baseUrl}/byEmail/${email}`);
  }

  /**
   * Find list of members and filter the list
   * System user will see all users, Account system will see all users of the account, registered user will get an error.
   * @Return: QueryResponse<Member>
   */
  find(search?: string, type?: UserTypeCode[], status?: UserStatusCode[], group?: string, sort?: string, page?: number, pageSize?: number) {
    const params = new Array();		
    if (search != null) { params.push(`search=${search}`); }
    if (type != null) { params.push(`type=${type}`); }
    if (status != null) { params.push(`status=${status}`); }
    if (group != null) { params.push(`group=${group}`); }
    if (sort != null) { params.push(`sort=${sort}`); }
    if (page != null) { params.push(`page=${page}`); }
    if (pageSize != null) { params.push(`pageSize=${pageSize}`); }

		return this.rest.get(`${this.baseUrl}`, ...params);
  }

  /**
   * Export list of members and filter
   * @Return: StreamContent
   */
  exportFormat(search?: string, type?: UserTypeCode[], status?: UserStatusCode[], sort?: string, format?: string, fields?: string[], fileName?: string) {
    const params = new Array();		
    if (search != null) { params.push(`search=${search}`); }
    if (type != null) { params.push(`type=${type}`); }
    if (status != null) { params.push(`status=${status}`); }
    if (sort != null) { params.push(`sort=${sort}`); }
    if (format != null) { params.push(`format=${format}`); }
    if (fields != null) { params.push(`fields=${fields}`); }
    if (fileName != null) { params.push(`fileName=${fileName}`); }

		return this.rest.download(`members`,`${this.baseUrl}/export`, ...params);
  }

  /**
   * Get access token for member
   * @Return: ActionResponse
   */
  getUserToken(id?: string, exp?: number) {
    return this.rest.get(`${this.baseUrl}/${id}/token/${exp}`);
  }

}
