
/*  
*/
export class EventsServiceGetIntegrationsRequestRequest {
 
    // Event Id 
    public id: string;
 
    // Sensor Id - the sensor which generated the event (medium response if this parameter is provided) 
    public sensorId: string;
 
    // Folder Id - the folder of the sensor which generated the event (fastest response if this parameter is provided) 
    public folderId: string;
 
    constructor(id?: string, sensorId?: string, folderId?: string) { 
        this.id = id;
        this.sensorId = sensorId;
        this.folderId = folderId;
    }

}


