import { Permission } from '../common/Permission';
import { BaseEntity } from '../entities/BaseEntity';

/* 
   Group is set of folders permissions 
*/
export class Group extends BaseEntity {
 
    // Account Id 
    public accountId: string;
 
    // Group name 
    public name: string;
 
    // Group description 
    public description: string;
 
    // Folder hierarchy depth 
    public depth: number;
 
    // Group permissions 
    public permissions: Permission[];
 
    // Entity Id 
    public id: string;
 
    // Entity document type (system field) 
    public _type: string;
 
    // When the entity was created [Epoch milliseconds Timestamp] 
    public createdOn: number;
 
    // When the entity was updated [Epoch milliseconds Timestamp] 
    public updatedOn: number;
 
    // Entity path in the hierarchy 
    public path: string;
 

}


