
/*  
*/
export class ResponseOfAddPoi {
 
    // A possible error code, if the creation was not successful. Equals zero on success 
    public code: number;
 
    // The given name to the POI 
    public name: string;
 
    // The given ID of the POI 
    public poiId: string;
 
    // The timestamp when the POI will expire, Epoch timestamp in milliseconds 
    public expireAt: string;
 
    constructor(code?: number, name?: string, poiId?: string, expireAt?: string) { 
        this.code = code;
        this.name = name;
        this.poiId = poiId;
        this.expireAt = expireAt;
    }

}


