export class SensorUsageReportRequest {
 
    // Sensor Id 
    public sensorId: string;
 
    // Year 
    public year: number;
 
    // Month 
    public month: number;
 
    constructor(sensorId?: string, year?: number, month?: number) { 
        this.sensorId = sensorId;
        this.year = year;
        this.month = month;
    }

}


