
/*  
*/
export class StaticWatchlist {
 
    // System ID of the watchlist 
    public iD: string;
 
    // User given name of the watchlist 
    public name: string;
 
    // 
    public type: string;
 
    // 
    public totalPOIs: number;
 
    constructor(iD?: string, name?: string, type?: string, totalPOIs?: number) { 
        this.iD = iD;
        this.name = name;
        this.type = type;
        this.totalPOIs = totalPOIs;
    }

}


