import { AccountStatusCode } from '../enums/AccountStatusCode';

/* 
   User Account info (returned by switch-account method) 
*/
export class UserAccountInfoAccount {
 
    // Account Id 
    public id: string;
 
    // Account name 
    public name: string;
 
    // Account status: UNDEFINED | ACTIVE | SUSPENDED 
    public status: AccountStatusCode;
 
    // Enable system administrators (non-account users) full access to account actions and account footage (sys admin and support users) 
    public sysAdminSupport: boolean;
 
    constructor(id?: string, name?: string, status?: AccountStatusCode, sysAdminSupport?: boolean) { 
        this.id = id;
        this.name = name;
        this.status = status;
        this.sysAdminSupport = sysAdminSupport;
    }

}


