import { PlatformTypeCode } from '../enums/PlatformTypeCode';

/* 
   Switch account request message 
*/
export class SwitchAccountRequest {
 
    // Account Id 
    public accountId: string;
 
    // Platform type for request 
    public platformType: PlatformTypeCode;
 
    constructor(accountId?: string, platformType?: PlatformTypeCode) { 
        this.accountId = accountId;
        this.platformType = platformType;
    }

}


