import { Attribute } from '../common/Attribute';
import { BoundingBox } from '../common/BoundingBox';
import { Embedding } from '../common/Embedding';

/* 
   Extended custom model results 
*/
export class ExternalModelResults {
 
    // Model Name 
    public modelName: string;
 
    // External model attributes 
    public attributes: Attribute[];
 
    // Instance bounding box in the frame 
    public boundingBox: BoundingBox;
 
    // Embedded data 
    public embedding: Embedding;
 
    // Key points values 
    public keypoints: number[];
 
    constructor(modelName?: string, attributes?: Attribute[], boundingBox?: BoundingBox, embedding?: Embedding, keypoints?: number[]) { 
        this.modelName = modelName;
        this.attributes = attributes;
        this.boundingBox = boundingBox;
        this.embedding = embedding;
        this.keypoints = keypoints;
    }

}


