import { PlatformTypeCode } from '../enums/PlatformTypeCode';

/* 
   Login parameters data model 
*/
export class LoginParams {
 
    // User email 
    public email: string;
 
    // User password 
    public password: string;
 
    // Access token - in case of re-login with existing token 
    public accessToken: string;
 
    // Platform type for login request 
    public platformType: PlatformTypeCode;
 
    constructor(email?: string, password?: string, accessToken?: string, platformType?: PlatformTypeCode) { 
        this.email = email;
        this.password = password;
        this.accessToken = accessToken;
        this.platformType = platformType;
    }

}


