
/*  
*/
export class SysApplianceConfigExportRequest {
 
    // Filter by Account Id 
    public accountId: string;
 
    // Free text filter by appliance Id, appliance name or machine Id 
    public search: string;
 
    // Filter by configuration Id 
    public configId: string;
 
    // Filter by configuration version Id 
    public versionId: string;
 
    // Find devices that their desired configuration doesn't match the actual configuration 
    public unmatched: boolean;
 
    // Export format: csv | json | xml 
    public format: string;
 
    // Set download file name (without extension) 
    public fileName: string;
 
    constructor(accountId?: string, search?: string, configId?: string, versionId?: string, unmatched?: boolean, format?: string, fileName?: string) { 
        this.accountId = accountId;
        this.search = search;
        this.configId = configId;
        this.versionId = versionId;
        this.unmatched = unmatched;
        this.format = format;
        this.fileName = fileName;
    }

}


